export async function generateNodeFromPrompt(prompt) {
  // Convert the prompt to JSON format for better structure and clarity
  const jsonPrompt = {
    role: "system",
    content: {
      expertise: "business process automation assistant",
      task: "Analyze the user's request and create a detailed workflow node with complete information",
      required_elements: [
        "Choose the single best node type",
        "Assign to appropriate role (e.g., 'Founder', 'Legal Team', 'Marketing Manager', 'Operations Manager')",
        "Set realistic priority (CRITICAL, HIGH, MEDIUM, LOW)",
        "Estimate duration (e.g., '2 weeks', '3 days', '1 month')",
        "List ALL required documents for this task"
      ],
      document_categories: {
        legal_documents: ["contracts", "licenses", "registrations"],
        financial_documents: ["budgets", "projections", "invoices"],
        hr_documents: ["job descriptions", "policies"],
        marketing_materials: ["plans", "content", "guidelines"],
        operational_documents: ["SOPs", "checklists", "agreements"],
        compliance_documents: ["permits", "certifications", "insurance"]
      },
      quality_standards: {
        specificity: "Be specific and comprehensive",
        practicality: "Think about what someone would actually need to complete this task"
      },
      available_node_types: availableNodeTypes
    }
  };

  const fullPrompt = `
${JSON.stringify(jsonPrompt, null, 2)}

User Request: "${prompt}"
`;

  try {
    console.log("Calling AI model for node generation with prompt:", prompt.substring(0, 100) + "...");
    
    const response = await ai.models.generateContent({
      model: "gemini-2.5-flash",
      contents: fullPrompt,
      config: {
        responseMimeType: "application/json",
        responseSchema: singleNodeSchema,
      },
    });

    console.log("Received response from AI model for node generation");
    
    const jsonString = response.text.trim();
    console.log("Response text:", jsonString.substring(0, 200) + "...");
    
    const parsedResponse = JSON.parse(jsonString);

    if (
      parsedResponse &&
      typeof parsedResponse === 'object' &&
      'type' in parsedResponse &&
      'label' in parsedResponse &&
      'assignedTo' in parsedResponse &&
      'priority' in parsedResponse &&
      availableNodeTypes.includes(parsedResponse.type)
    ) {
      return parsedResponse;
    } else {
      throw new Error("AI response did not match the required schema.");
    }
  } catch (error) {
    console.error("Error generating node from prompt:", error);
    // Provide more detailed error information
    if (error.message.includes("API key")) {
      throw new Error("Invalid API key. Please check your Gemini API configuration.");
    } else if (error.message.includes("quota")) {
      throw new Error("API quota exceeded. Please try again later or check your Gemini API plan.");
    } else if (error.message.includes("parse")) {
      throw new Error("Failed to parse AI response. The response format was unexpected.");
    } else {
      throw new Error(`Failed to communicate with the AI model: ${error.message}`);
    }
  }
}